.clearfix::after {
  content: "";
  display: block;
  clear: both;
}

.chatApp__convTimeline {
  display: flex;
  max-height: calc(100% -10rem);
  /* height: 400px; */
  padding: 1rem;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  flex-direction: column-reverse;
  overflow-y: auto;
}
.chatApp__convMessageItem {
  padding: 1rem 0 0 0;
  opacity: 0;
  transition: all 0.15s ease-in-out;
  animation: fadeNewMessage 0.5s;
  animation-fill-mode: forwards;
}
.chatApp__convMessageAvatar {
  width: 40px;
  height: 40px;
  border-radius: 40px;
  image-rendering: -webkit-optimize-contrast;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.chatApp__convMessageValue {
  position: relative;
  max-width: 50%;
  min-height: 40px;
  padding: 0.75rem;
  border-radius: 4px;
  font-size: 14px;
  line-height: 18px;
  font-weight: bold;
  box-shadow: 0px 10px 10px -8px rgba(0, 0, 0, 0.08);
  box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
.chatApp__convMessageItem--left .chatApp__convMessageAvatar {
  float: left;
}
.chatApp__convMessageItem--left .chatApp__convMessageValue {
  float: left;
  text-align: left;
  margin: 0 0 0 1rem;
}
.chatApp__convMessageItem--right .chatApp__convMessageAvatar {
  float: right;
}
.chatApp__convMessageItem--right .chatApp__convMessageValue {
  float: right;
  text-align: right;
  margin: 0 1rem 0 0;
}

@keyframes fadeNewMessage {
  0% {
    opacity: 0;
    transform: translateY(1rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
